import * as React from "react"
import PropTypes from "prop-types"
import {Container} from "react-bootstrap" 
import Pcard from "./pcard"

import * as gStyles from "./gallery.module.css"

/*
 <>
    {p.pid !== -1} 
</>
^^^ this looks weird, idea is to have a conditional that catches bad (i.e. unset) data. 
idk if this will ever fire though, 
because the -1 pid will only ever happen on a miss-initialized pcard, 
which is the thing the conditional controls. so hmm
so maybe its a place holder for gallery logic or sth. 
*/

const Gallery = ({ title, bucket, paint_data }) => (
    <Container className={gStyles.grid}>
        {paint_data.map((p) =>(
            <>
                {p.pid !== 0}  
                <Pcard bucketurl={bucket} pid={p.pid} ptitle={p.ptitle} pyear={p.pyear} pmedium={p.pmedium} pdims={p.pdims} pframed={p.pframed} pavailable={p.pavailable}/>
            </>
        ))}
    </Container>

)

Gallery.propTypes = {
    title: PropTypes.string,
    paint_data: PropTypes.object,
    bucket: PropTypes.string
}

Gallery.defaultProps = {
    title: ``,
    paint_data: {},
    bucket: 'https://jpart-gallery-images.s3.us-east-2.amazonaws.com/6.19/'
}

export default Gallery